import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { Button, TransitionablePortal, Segment, Icon } from 'semantic-ui-react';

class Nav extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false,
      path: this.props.path
    };
  }

    logout = () => {
      window.localStorage.removeItem('loggedUser');
      window.location.assign('/')
    }

    show = () => {
      this.setState({
        show: !this.state.show,
      });
    }

    render() {
      const white = { color: '#004d99', paddingTop: '2em'}
      const animation = 'horizontal flip'
      const duration = 500
      const desktop = window.innerWidth > 1000

      return (
        <div style={{display: "inline-block", right: "0", position: "absolute", top: "0", paddingTop: "2em", paddingRight: "2em"}}>
          <Icon name='bars' style={{color: "white"}} size="big" onClick={() => this.show()}/>
          <TransitionablePortal
            open={this.state.show} 
            transition={{ animation, duration }}
            onClose={() => this.show()}>
            <Segment 
            style={{textAlign: "center", padding: "2em",
            right: desktop ? '18%' : "1%",
            position: 'fixed',
            top: desktop ? '6%' : "0%",
            border: "1px solid #919499"
            }} 
            >
            <img 
                src="https://i.imgur.com/tQ6HhoS.png" 
                height="50px"
                style={{display: "block",
                 margin: "0 auto"}}
                
              />
            <br />
            <Link style={white} to="/">Etusivu</Link>
            <br />
            <br />

            <Link style={white} to="/kone">Vaalikone</Link>
            <br />
            <br />

            <Link style={white} to="/kysymykset">Kysymykset</Link>
            <br />
            <br />

            <Link style={white} to="/vastaukset">Vastaukset</Link>
            <br />
            <br />

            <Link style={white} to="/kategoriat">Kategoriat</Link>
            <br />
            <br />

            {window.localStorage.getItem('loggedUser') &&
              <Link style={white} to="/lisaa">Uusi kysymys</Link>
            }
            {window.localStorage.getItem('loggedUser') &&
              <div style={{ padding: '1em' }}>
            <Button onClick={() => this.logout()} size="tiny" type="submit"> Kirjaudu ulos</Button>
              </div>
            }
            </Segment>
          </TransitionablePortal>
        </div>
      );
    }
}

const mapStateToProps = state => ({
  user: state.user,
});

export default connect(mapStateToProps)(Nav);
